<div>
	<div class="wrap_gray">
		<div class="wrap wrap_classroom">
		   <h2></h2>
		   <ul class="class_list class_list_center">
		   	  <class-item></class-item>
		   </ul>
			
		</div>
	</div>
	<div class="wrap_gray2">
		<div class="wrap wrap_news">
		  <div class="wrap_news_length">
			<div class="titles"><img src="../../images/light.png"><span>智慧校园新闻</span></div>
			<div class="more"><router-link to="/news">更多 ></router-link></div>
		  </div>
			<div class="news_left" v-for="(data,index) in news_img" :newsItem="data" :key="data.id">
			  <router-link :to="'/newsDetails?id=' + data.id">
				<img :src="data.src" />
			  </router-link>
			</div>
			<div class="news_right">
				<ul>
					<news-item v-for="(data, index) in newsList" :newsItem="data" :key="data.id"></news-item>
				</ul>
			</div>
		</div>
	</div>
