				<li class="first"><router-link to="/center">核心优势</router-link></li>
				<li class="second"><router-link to="/team">专业团队</router-link></li>
				<li class="third"><router-link to="/first">解决方案</router-link></li>
				<li class="fourth"><router-link to="/service">服务流程</router-link></li>
				<li class="fifth"><router-link to="/aboutus">关于我们</router-link></li>
			</ul>
		</div>
	</div>
</div>
</template>

<script>
import axios from 'axios';
import classItem from '@/components/classItem';
import newsItem from '@/components/newsItem';

export default{
  name: 'index',
  data(){
  	return {
  		newsList:[],
  		news_img:[],
  	}
  },
  created(){
  	axios.get('https://www.easy-mock.com/mock/5a0bad8922eff44db04955c0/example_1510714761014/indexData').then(response=>{
  		this.newsList = response.data.data.newsList;
  		this.news_img = response.data.data.news_img;
  	})
  },
  components: {
    classItem,
    newsItem,
  },
}
</script>

