"use strict";
const commonMthods = {
  
    getUrlPram : function(name) {
        var reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)"),
            r = window.location.search.substr(1).match(reg);
        if(r!=null) {
            return  decodeURI(r[2]);
        }
        return null;
    },

    format1 : function(time) {
        var date = new Date(time);
        function two(day) {
            if( day<10 ) {
                return '0' + day;
            }
            return day;
        }
        return  two(date.getDate());
    },

    format2 : function(time) {
        var date = new Date(time);
        function two(day) {
            if( day<10 ) {
                return '0' + day;
            }
            return day;
        }
        return  date.getFullYear()+ '-' + two(date.getMonth()+1);
    },
    
     format : function(time) {
        var date = new Date(time);
        function two(day) {
            if( day<10 ) {
                return '0' + day;
            }
            return day;
        }
        return  date.getFullYear()+ '-' + two(date.getMonth()+1) + '-' +
        two(date.getDate());
    },

    // 设置Cookie
    setCookie : function(name, value) {
        var Days = .5;
        var exp = new Date();
        exp.setTime(exp.getTime() + Days*24*60*60*1000);
        document.cookie = name + "="+ escape (value) + ";expires=" + exp.toGMTString() + "; path=/";
    },

    // 获取Cookie
    getCookie : function(cname) {
        var name = cname + "=";
        var ca = document.cookie.split(';');
        for(var i=0; i<ca.length; i++) {
            var c = ca[i];
            while (c.charAt(0)==' ') c = c.substring(1);
            if (c.indexOf(name) != -1) return unescape(c.substring(name.length, c.length));
        }
        return "";
    },

    // 删除Cookie
    delCookie : function(name) {
        var exp = new Date();
        exp.setTime(exp.getTime() - 1);
        var cval= commonMthods.getCookie(name);
        if(cval!=null)
            document.cookie= name + "="+cval+";expires="+exp.toGMTString();
    },

    toFixed : function(num, s) {
        var times = Math.pow(10, s),
            des = num * times + 0.5;
        des = parseInt(des, 10) / times;
        return des;
    }

};
export default commonMthods;


