<template>
	<li>
		<router-link :to="'/newsDetails?id=' + newsItem.id">
		  <div class="time">
		  	<p class="time-day" v-html="format1(newsItem.time)"></p>
		  	<p class="time-date" v-html="format2(newsItem.time)"></p>
		  </div>
		  <div class="news-title">
		  	<h2>{{newsItem.title}}</h2>
		  	<div class="news-summary">
		  		<p>{{newsItem.summary}}</p>
		  	</div>
		  </div>
        </router-link>
	</li>
</template>

<script>
import commonMethods from '../js/common.js';
	export default{
		name:'newsItem',
		props:{
			newsItem:{
				required:true,
				type:Object
			}
		},
		methods:{
			format1(time){
        		var date = new Date(time);
        		return this.two(date.getDate());
        	},
        	two(day) {
        		if( day<10 ) {
        			return '0' + day;
        		}
        		return day;
        	},
        	format2(time){
        		var date = new Date(time);
        		return date.getFullYear()+'-'+this.two(date.getMonth()+1);
        	},
        	
		}
	}
</script>

<style scoped>
	.news_right li{
		position:relative;
		padding-left: 80px;
		min-height: 137px;
		height: auto !important;
		border-bottom: 1px solid #ccc;
		margin-bottom: 10px;
	}
	.news_right li:nth-child(3){
		border-bottom:none;
	}
	.news_right li:hover h2{
		color:#c06d05;
	}
	.news_right li:hover .news-summary{
		color:#c06d05;
	}
	.time{
		width:86px;
		position: absolute;
		left: -20px;
		top: 30px;
		text-align: center;
		font-family: Arial;
	}
	.time-day{
		font-size: 52px;
		line-height: 40px;
		display: block;
		line-height: 50px;
		color: #e99014;
	}
	.time-date{
		display: block;
		color: #999;
		line-height: 40px;
		font-size:14px;
	}
	.news-title{
		margin-bottom:10px;
		padding-top:30px;
	}
	.news-title h2{
		font-size:18px;
		color:#000000;
		font-weight:normal;
	}
	.news-summary{
		color: #808080;
		font-size: 14px;
		margin-bottom: -8px;
		margin-top:7px;
	}
	.news-summary p{
		line-height:20px;
	}
</style>


