import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'

Vue.use(Router)

export default new Router({
  routes: [
     {
      path: '/',
      name: 'index',
      component: index
    },
     {
    	path: '/center',
    	name: 'index',
    	component: resolve => require(['../pages/center/index.vue'], resolve)
    },
    {
      path: '/team',
      name: 'index',
      component: resolve => require(['../pages/team/index.vue'], resolve)
    },
    {
      path: '/service',
      name: 'index',
      component: resolve => require(['../pages/service/index.vue'], resolve)
    },
    {
      path: '/aboutus',
      name: 'index',
      component: resolve => require(['../pages/aboutus/index.vue'], resolve)
    },
    {
      path: '/news',
      name: 'index',
      component: resolve => require(['../pages/news/index.vue'], resolve)
    },
    {
      path: '/newsDetails',
      name: 'index',
      component: resolve => require(['../pages/news/newsDetails.vue'], resolve)
    },
    {
      path: '/first',
      name: 'first',
      component: resolve => require(['../pages/Culture/first.vue'], resolve)
    },
     {
      path: '/second',
      name: 'first',
      component: resolve => require(['../pages/Culture/second.vue'], resolve)
    },
     {
      path: '/third',
      name: 'first',
      component: resolve => require(['../pages/Culture/third.vue'], resolve)
    },
     {
      path: '/fourth',
      name: 'first',
      component: resolve => require(['../pages/Culture/fourth.vue'], resolve)
    },
     {
      path: '/fifth',
      name: 'first',
      component: resolve => require(['../pages/Culture/fifth.vue'], resolve)
    },
  ]
})



