<template>
	<div class="top_bg">
		<div class="head_top_wrap">
			<div class="header_top">
				<div class="logo">
					<img src="../images/1-logo.png">
				</div>
				<div class="information">
					<div class="phone"><span>400 8566 800</span></div>
					<div class="qq"><span><a href="http://wpa.qq.com/msgrd?v=3&uin=288107231&site=qq&menu=yes">288107231</a></span></div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		name:'m-header',
	}
</script>

<style scoped>
	.top_bg{
		width: 100%;
		height: 700px;
		background: url(../images/bannerClass.jpg) center center no-repeat;
		position: relative;
	}
	.header_top_wrap{
		width: 100%;
		height: 97px;
		position: absolute;
		z-index: 10;
	}
	.header_top{
		width: 1200px;
		height: 97px;
		margin: 0 auto;
	}
	.logo{
		float: left;
		width: 249px;
		height: 97px;
	}
	.logo img{
		height: 40px;
		width: 249px;
		display: block;
		margin-top: 20px;
	}
	.information{
		float: right;
		height: 60px;
		line-height: 60px;
	}
	.information div{
		display:inline-block;
		float:left;
		color:#ffffff;
		vertical-align:middle;
	}
	.information .phone{
		background:url(../images/phone.png) 0% 50% no-repeat;
		margin-right:26px;
	}
	.information .qq{
		background:url(../images/qq.png) 0% 50% no-repeat;
	}
	.information .phone span{
		margin-left:23px;
	}
    .information .qq span{
		margin-left:23px;
	}
	 .information .qq span a{
	 	color:#ffffff;
	 }
	.information .qq:hover{
       background:url(../images/qq1.png) 0% 50% no-repeat;
	}
	.information .qq:hover span a{
       color:#e99014;
	}
</style>


